import { Box, useColorModeValue } from '@chakra-ui/react';
import * as React from 'react';
import '../../assets/css/animation.css';
import { LogoLight } from '../../components/Navbar/Logo';

export default function SimpleSplashScreen() {
    const [className, SetclassName] = React.useState('main-div3');

    // React.useEffect(() => {
    // });
    return (
        <Box
            position={'absolute'}
            zIndex={999}
            height={'100vh'}
            width={'100vw'}
            bg={useColorModeValue('white.100', 'gray.900')}
        >
            <Box className="wrapper">
                <Box 
                    p={8} 
                    bg={useColorModeValue('white.100', 'gray.900')} 
                    className={`main-div ${className}`}
                >
                    <LogoLight h={{base: 100, md: 150}} />
                </Box>
                {/* <Box className="main-div main-div3">
                    <h1>Moment</h1>
                </Box> */}
            </Box>
        </Box>
    );
}