export default function TestimonialSlider() {
    return (
        <Stack w={'full'}>
            <Stack textAlign={'center'}>
                <Heading fontWeight={'bold'} fontSize={'4xl'}>We offers services that clients really wants</Heading>
            </Stack>
            <ServicesCarousel />
        </Stack>
    );
}