import React, { useEffect } from 'react';
import { Box, Flex, Heading, Text, Button, useColorModeValue, useBreakpointValue } from '@chakra-ui/react';
import { Link } from 'react-router-dom';
import Lottie from 'react-lottie';
import RocketAnimationData from '../../assets/lottie-files/rocket-animation.json';
import SplashLoaderWithDevice from '../../assets/lottie-files/splash-loader-with-device.json';
// import SplashLoadingAnimationLg from '../../assets/lottie-files/splash-loader-with-device.json';
import SplashLoadingAnimationLg from '../../assets/lottie-files/progress-bar-gradient.json';
import { LogoLight, LogoDark } from '../../components/Navbar/Logo';
import '../../assets/css/effects.css';
import { Bounce, JackInTheBox } from 'react-awesome-reveal';

export default function LoadingScreenWithLogo({ hideSplashScreen }: any) {
    const Logo = useColorModeValue(
        LogoDark,
        LogoLight
    );

    const LoadingAnimation = {
        loop: true,
        autoplay: true,
        animationData: SplashLoadingAnimationLg,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice",
        },
    };

    const [className, setClassName] = React.useState('');

    useEffect(() => {
        setTimeout(() => {
            setClassName('hithere');
            setTimeout(() => {
                setClassName('pulse');
            }, 1500);
        }, 1500);
    }, []);

    return (
        <Box
            position={'absolute'}
            zIndex={999}
            height={'100vh'}
            width={'100vw'}
            bg={useColorModeValue('white', 'gray.900')}
        >
            <Flex direction={'column'} gap={{ base: 12, md: 12, lg: 12 }} className="wrapper">
                <Bounce>
                    <Logo h={{ base: 70, md: 150 }} className={`${className}`} />
                </Bounce>
                <JackInTheBox>
                    <Lottie
                        options={LoadingAnimation}
                        // width={60}
                        style={{
                            width: useBreakpointValue({ base: '250px', md: '500px' }),
                            height: 'fit-content',
                            border: '1px solid transparent',
                            borderRadius: '100px'
                        }}
                        eventListeners={[
                            {
                                eventName: 'loopComplete',
                                callback: () => {
                                    hideSplashScreen(false)
                                }
                            }
                        ]}
                    />
                </JackInTheBox>
            </Flex>
        </Box>
    );
}

function LoadingScreenWithLogoDeprecated() {

    const Logo = useColorModeValue(
        LogoDark,
        LogoLight
    );

    const LoadingAnimation = {
        loop: true,
        autoplay: true,
        animationData: SplashLoadingAnimationLg,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice",
        },
    };

    const RocketAnimation = {
        loop: true,
        autoplay: true,
        animationData: RocketAnimationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice",
        },
    };
    const [animationHooks, setAnimationHooks] = React.useState({ showLoadingAnimation: false, showRocketAnimation: false });

    function ShowLoader() {
        return (
            !animationHooks.showLoadingAnimation ?
                (
                    <Lottie
                        options={LoadingAnimation}
                        // width={60}
                        style={{
                            width: useBreakpointValue({ base: '100%', md: '100%', lg: '70%', xl: '55%' }),
                            height: 'fit-content'
                        }}
                        eventListeners={[
                            {
                                eventName: 'loopComplete',
                                callback: () => {
                                    setAnimationHooks({ ...animationHooks, showLoadingAnimation: true })
                                }
                            }
                        ]}
                    />
                )
                :
                (
                    <Lottie
                        options={RocketAnimation}
                        style={{
                            width: useBreakpointValue({ base: '100%', md: '100%', lg: '60%', xl: '40%' }),
                            height: 'fit-content'
                        }}
                        eventListeners={[
                            {
                                eventName: 'loopComplete',
                                callback: () => {
                                    setAnimationHooks({ ...animationHooks, showRocketAnimation: true })

                                }
                            }
                        ]}
                    />
                )

        )
    };
    // useEffect(() => {
    //     setTimeout(() => {
    //         setChangeLoadingAnimation(true);
    //     }, 4000);
    // })
    const [className, setClassName] = React.useState('grow-once');
    useEffect(() => {
        if (animationHooks.showRocketAnimation) {
            setTimeout(() => {
                setClassName('hithere');
                setTimeout(() => {
                    setClassName('pulse');
                }, 1500);
            }, 1500);
        }
    }, [animationHooks]);
    return (
        <Box
            position={'absolute'}
            zIndex={999}
            height={'100vh'}
            width={'100vw'}
            bg={useColorModeValue('white', 'gray.900')}
        >
            <Box className="wrapper">
                {
                    (!animationHooks.showRocketAnimation) ?
                        (
                            <ShowLoader />
                        )
                        :
                        (
                            <Logo h={{ base: 100, md: 150 }} className={`${className}`} />
                        )
                }
                {/* {
                    <button onClick={e => setAnimationHooks({...animationHooks, showRocketAnimation: true})}>Click me</button>
                } */}
            </Box>
        </Box>
    );
}