import {
  Box,
  Button,
  Flex,
  FormControl,
  FormLabel,
  Heading,
  IconButton,
  Input,
  InputGroup,
  InputLeftElement,
  Link,
  Stack,
  Textarea,
  Tooltip,
  useClipboard,
  useColorModeValue,
  useToast,
  VStack,
} from '@chakra-ui/react';
import React, { useState } from 'react';
import { BsPerson } from 'react-icons/bs';
import { FaFacebook as FaGithub, FaTwitter, FaLinkedin } from 'react-icons/fa';
// import { MdEmail, MdOutlineEmail } from 'react-icons/md';
import { AiFillMail, AiOutlineMail } from 'react-icons/ai';
import { useNavigate } from 'react-router';
import { addLead } from '../../../utils/FirebaseUtils';

export function ContactForm() {
  const { hasCopied, onCopy } = useClipboard('example@example.com');
  const toast = useToast();

  const [name, setName]: any = useState('');
  const [email, setEmail]: any = useState('');
  const [phoneNumber, setPhoneNumber]: any = useState('');
  const [message, setMessage]: any = useState('');

  function handleSubmit() {
    addLead(name, email, phoneNumber, message);
    setName('');
    setEmail('');
    setMessage('');
    toast({
      status: "success",
      title: "Thank you for contacting us!",
      description: "Someone will reach out to you shortly!",
      isClosable: false,
      duration: 5000,
      position: 'bottom'
    })
  }
  return (
    <Flex
      id="contact">
      <Box
        borderRadius="lg"
        m={{ base: 5, md: 16, lg: 10 }}
        p={{ base: 5, lg: 16 }}>
        <Stack
          spacing={{ base: 4, md: 8 }}
        >
          <Heading
            textAlign={'center'}
            fontSize={'4xl'} fontWeight={'bold'}>
            Get In Touch
          </Heading>
          <Stack
            spacing={{ base: 4, md: 8, lg: 20 }}
            direction={{ base: 'column', md: 'row' }}>
            <Stack
              align="center"
              justify="space-around"
              direction={{ base: 'row', md: 'column' }}>
              <Tooltip
                label={hasCopied ? 'Email Copied!' : 'Copy Email'}
                closeOnClick={false}
                hasArrow>
                <IconButton
                  aria-label="email"
                  variant="ghost"
                  size="lg"
                  fontSize="3xl"
                  icon={<AiFillMail />}
                  _hover={{
                    bg: 'blue.500',
                    color: useColorModeValue('white', 'gray.700'),
                  }}
                  onClick={onCopy}
                  isRound
                />
              </Tooltip>

              <Link href="#">
                <IconButton
                  aria-label="github"
                  variant="ghost"
                  size="lg"
                  fontSize="3xl"
                  icon={<FaGithub />}
                  _hover={{
                    bg: 'blue.500',
                    color: useColorModeValue('white', 'gray.700'),
                  }}
                  isRound
                />
              </Link>

              <Link href="#">
                <IconButton
                  aria-label="twitter"
                  variant="ghost"
                  size="lg"
                  icon={<FaTwitter size="28px" />}
                  _hover={{
                    bg: 'blue.500',
                    color: useColorModeValue('white', 'gray.700'),
                  }}
                  isRound
                />
              </Link>

              <Link href="#">
                <IconButton
                  aria-label="linkedin"
                  variant="ghost"
                  size="lg"
                  icon={<FaLinkedin size="28px" />}
                  _hover={{
                    bg: 'blue.500',
                    color: useColorModeValue('white', 'gray.700'),
                  }}
                  isRound
                />
              </Link>
            </Stack>

            <Box
              bg={useColorModeValue('white', 'gray.700')}
              borderRadius="lg"
              p={8}
              color={useColorModeValue('gray.700', 'whiteAlpha.900')}
              shadow="base">
              <VStack spacing={5}>
                <FormControl isRequired>
                  <FormLabel>Name</FormLabel>

                  <InputGroup>
                    <InputLeftElement children={<BsPerson />} />
                    <Input type="text" name="name" placeholder="Your Name" value={name} onChange={e => setName(e.target.value)} />
                  </InputGroup>
                </FormControl>

                <FormControl isRequired>
                  <FormLabel>Email</FormLabel>

                  <InputGroup>
                    <InputLeftElement children={<AiOutlineMail />} />
                    <Input
                      type="email"
                      name="email"
                      placeholder="Your Email"
                      value={email}
                      onChange={e => setEmail(e.target.value)}
                    />
                  </InputGroup>
                </FormControl>

                <FormControl isRequired>
                  <FormLabel>Phone Number</FormLabel>

                  <InputGroup>
                    <InputLeftElement children={<AiOutlineMail />} />
                    <Input
                      type="tel"
                      name="phoneNumber"
                      placeholder="Your Phone Number"
                      value={phoneNumber}
                      onChange={e => setPhoneNumber(e.target.value)}
                    />
                  </InputGroup>
                </FormControl>

                <FormControl isRequired>
                  <FormLabel>Message</FormLabel>

                  <Textarea
                    name="message"
                    placeholder="Your Message"
                    rows={6}
                    resize="none"
                    value={message}
                    onChange={e => setMessage(e.target.value)}
                  />
                </FormControl>

                <Button
                  bg={'#F04037'}
                  color={'white'}
                  _hover={{ bg: '#C31A12' }}
                  _active={{ bg: '#8a1611' }}
                  isFullWidth
                  onClick={handleSubmit}
                >
                  Send Message
                </Button>
              </VStack>
            </Box>
          </Stack>
        </Stack>
      </Box>
    </Flex>
  );
}
