import React from 'react';
import { Route } from 'react-router';
import StartingWidget from '../pages/landing-pages/StartingWidget';
import RouteSwitch from '../components/Routes/RouteSwitch';
import LandingPage from '../pages/landing-pages/landing-page-1';
import NotFound from '../pages/NotFound/404';
import Portfolio from '../pages/Portfolio/Portfolio';
import Services from '../pages/Services/Services';
import AboutUs from '../pages/about-us/About';
import ContactUs from '../pages/contact-us/Contact';
import Blogs from '../pages/Blogs/Blogs';
import Stats from '../components/Statistics/Stats';
import LoadingScreenWithLogo from '../pages/Splash-Screens/LoadingScreenWithLogo';
import SimpleSplashScreen from '../pages/Splash-Screens/SimpleSplashScreen';
import Technology from '../pages/Technology/Technology';
import Login from '../pages/admin/authentication/login';
import { Dashboard } from '../pages/admin/pages/Dashboard';

export default function Routes() {
    return (
        <RouteSwitch>
            <Route path='/' element={<LandingPage />} />
            <Route path='/portfolio' element={<Portfolio />} />
            <Route path='/services' element={<Services />} />
            <Route path='/about-us' element={<AboutUs />} />
            <Route path='/contact-us' element={<ContactUs />} />
            <Route path='/blogs' element={<Blogs />} />
            <Route path='/splashscreen' element={<LoadingScreenWithLogo />} />
            <Route path='/simplesplashscreen' element={<SimpleSplashScreen />} />
            <Route path='/technologies' element={<Technology />} />

            {/* Admin Routes */}
            <Route path='/admin/login' element={<Login />} />
            <Route path='/admin/dashboard' element={<Dashboard />} />
            <Route path='/TechFatura/admin/login' element={<Login />} />
            <Route path='/TechFatura/admin/dashboard' element={<Dashboard />} />

            {/* Not Found Route */}
            <Route path='*' element={<NotFound />} />
        </RouteSwitch>
    );
};