import * as React from 'react';
import {
    Box,
    Heading,
    Flex,
    Image,
    useColorModeValue,
    useBreakpointValue
} from '@chakra-ui/react';
import About1 from '../../assets/images/about-1.png';
import About2 from '../../assets/images/about-2.png';
import { Slide, JackInTheBox } from 'react-awesome-reveal';

export default function About() {
    return (
        <Box
            as="section"
            bg={useColorModeValue('white', 'gray.900')}
            px={{ base: 6, md: 4, lg: 32 }}
            w={'full'}
            py={12}
            gap={6}
        >
            <Heading
                textAlign={'center'}
                size="xl"
                mb={8}
                px={{ base: 6, md: 32, lg: 64 }}
                fontWeight={'bold'}
                fontSize={'4xl'}
                letterSpacing={'wide'}
                lineHeight="45px">
                We love working with entrepreneurs who intend to create epic products with great potential!
            </Heading>
            <Flex
                mb="12"
                justifyContent={'center'}
                direction={{ base: 'column', md: 'row' }}
                textAlign={{ base: 'center', md: 'inherit' }}
                gap={4}
            >
                <Slide direction='left'>
                    <Box>
                        <Image
                            rounded={'lg'}
                            height={{ base: '100%', md: "xl" }}
                            src={About1}
                        />
                    </Box>
                </Slide>
                <Slide direction={'left'}>
                    <Box>
                        <Image
                            rounded={'lg'}
                            height={{ base: '100%', md: "xl" }}
                            src={About2}
                        />
                    </Box>
                </Slide>
            </Flex>
        </Box>
    )
}
