import { initializeApp } from "firebase/app";
import {
    GoogleAuthProvider,
    getAuth,
    signInWithPopup,
    signInWithEmailAndPassword,
    createUserWithEmailAndPassword,
    sendPasswordResetEmail,
    signOut,
} from "firebase/auth";
import {
    getFirestore,
    query,
    getDocs,
    collection,
    where,
    addDoc,
} from "firebase/firestore";

const firebaseConfig = {
    apiKey: "AIzaSyD-2BYMWCcIQr4qYUHCV8P0TFy6vibYKNw",
    authDomain: "tech-fatura.firebaseapp.com",
    projectId: "tech-fatura",
    storageBucket: "tech-fatura.appspot.com",
    messagingSenderId: "1026733507130",
    appId: "1:1026733507130:web:a7c6748a0bffc29f416b03",
    measurementId: "G-DJDZ4TBWWR"
};
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);
const googleProvider = new GoogleAuthProvider();
const signInWithGoogle = async () => {
    try {
        const res = await signInWithPopup(auth, googleProvider);
        const user = res.user;
        const q = query(collection(db, "users"), where("uid", "==", user.uid));
        const docs = await getDocs(q);
        if (docs.docs.length === 0) {
            await addDoc(collection(db, "users"), {
                uid: user.uid,
                name: user.displayName,
                authProvider: "google",
                email: user.email,
            });
        }
    } catch (err: any) {
        console.error(err);
        alert(err.message);
    }
};
const logInWithEmailAndPassword = async (email: any, password: any) => {
    try {
        await signInWithEmailAndPassword(auth, email, password);
    } catch (err: any) {
        console.error(err);
        alert(err.message);
    }
};
const registerWithEmailAndPassword = async (name: any, email: any, password: any) => {
    try {
        const res = await createUserWithEmailAndPassword(auth, email, password);
        const user = res.user;
        await addDoc(collection(db, "users"), {
            uid: user.uid,
            name,
            authProvider: "local",
            email,
        });
    } catch (err: any) {
        console.error(err);
        alert(err.message);
    }
};
const sendPasswordReset = async (email: any) => {
    try {
        await sendPasswordResetEmail(auth, email);
        alert("Password reset link sent!");
    } catch (err: any) {
        console.error(err);
        alert(err.message);
    }
};

const logout = () => {
    signOut(auth);
};

const fetchAllLeads = async () => {
    try {
        const q = query(collection(db, "leads"));
        const doc = await getDocs(q);
        console.log(doc)
        return doc.docs.map(d => d.data());
    } catch (err) {
        console.error(err);
        return err;
    }
}

const addLead = async (name: string, email: string, phoneNumber: string, message: string) => {
    try {
        await addDoc(collection(db, "leads"), { email, name, phoneNumber, message, created: new Date().toISOString() });
        return true;
    } catch (err: any) {
        return err;
    }
}


const fetchAllPaymentLinks = async () => {
    try {
        const q = query(collection(db, "payment-links"));
        const doc = await getDocs(q);
        return doc.docs.map(d => d.data());
    } catch (err) {
        console.error(err);
        return err;
    }
}

const createPaymentLink = async (
    agentName: string,
    customerName: string,
    email: string,
    phoneNumber: string,
    amount: number
) => {
    try {
        await addDoc(collection(db, "payment-links"), { agentName, customerName, email, phoneNumber, amount, created: new Date().toISOString() });
    } catch (err: any) {
        return err;
    }
}

export {
    auth,
    db,
    signInWithGoogle,
    logInWithEmailAndPassword,
    registerWithEmailAndPassword,
    sendPasswordReset,
    logout,
    fetchAllLeads,
    addLead,
    fetchAllPaymentLinks,
    createPaymentLink
};