import * as React from 'react'
import {
    Box,
    Button,
    Divider,
    Flex,
    Stack,
    StackDivider,
    Text,
    useColorModeValue as mode,
    HStack,
    IconButton,
    useDisclosure,
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalCloseButton,
    ModalBody,
    ModalFooter,
    FormControl,
    FormLabel,
    Input,
    useToast
} from '@chakra-ui/react'
import { HiLogout, HiUserCircle, HiPencilAlt, HiTrash, HiPlus } from 'react-icons/hi'
import { auth, createPaymentLink, fetchAllLeads, fetchAllPaymentLinks, logout } from "../../../utils/FirebaseUtils";
import { useAuthState } from "react-firebase-hooks/auth";
import { Link as ReactLink, useNavigate } from "react-router-dom";

interface DescriptionProps {
    name: string
    children?: React.ReactNode
    email: string
    phoneNumber?: string
}

export const Description = (props: DescriptionProps) => {
    const { name, children, email, phoneNumber } = props
    return (
        <Box position="relative">
            <Box fontWeight="bold" maxW="xl">
                {name}
            </Box>
            <HStack fontSize="sm" fontWeight="medium" color={mode('gray.500', 'white')} divider={<StackDivider />} mt="1">
                {/* <Box as={HiUserCircle} fontSize="md" color="gray.400" /> */}
                <span>{email}</span>
                <span>{phoneNumber}</span>
            </HStack>
            <Box mt="3" maxW="xl" color={mode('gray.600', 'gray.200')}>
                {children}
            </Box>
            <HStack
                position={{ sm: 'absolute' }}
                top={{ sm: '0' }}
                insetEnd={{ sm: '0' }}
                mt={{ base: '4', sm: '0' }}
            >
                {/* <IconButton aria-label="Delete" icon={<HiTrash />} rounded="full" size="sm" /> */}
            </HStack>
        </Box>
    )
}


export const Dashboard = () => {
    const [user, loading, error] = useAuthState(auth);
    const { isOpen, onOpen, onClose } = useDisclosure();

    const toast = useToast();
    const navigate = useNavigate();

    const [usersList, setUsersList]: any = React.useState([]);
    const [paymentLinks, setPaymentLinks]: any = React.useState([]);

    const [customerName, setCustomerName] = React.useState('');
    const [agentName, setAgentName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [phoneNumber, setPhoneNumber] = React.useState('');
    const [amount, setAmount] = React.useState(0);

    React.useEffect(() => {
        if (!user) navigate("/admin/login");
        (async () => {
            const users = await fetchAllLeads();
            setUsersList(users);

            const paymentLinks = await fetchAllPaymentLinks();
            setPaymentLinks(paymentLinks);
        })();
    }, [user, loading]);


    async function handleCreatePaymentLinkClick() {
        await createPaymentLink(agentName, customerName, email, phoneNumber, amount);
        const paymentLinks = await fetchAllPaymentLinks();
        setPaymentLinks(paymentLinks);
        toast({
            status: "success",
            title: "New Payment Link Generated Successfully!",
            description: "your new payment link has been generated. Please check the payment link card for more information.",
            duration: 4000,
            isClosable: false,
            position: 'bottom'
        });
        onClose();
    }

    return (
        <Stack px={16} py={12}>
            {/* User Card */}
            <Box as="section" bg={mode('gray.100', 'gray.800')}>
                <Box maxW={{ base: 'xl', md: '7xl' }} mx="auto" px={{ md: '8' }}>
                    <Box
                        rounded={'lg'}
                        bg={mode('white', 'gray.700')}
                        maxW="7xl"
                        mx="auto"
                        shadow="base"
                        overflow="scroll"
                    >
                        <Flex align="center" justify="space-between" px="6" py="4">
                            <Text as="h3" fontWeight="bold" fontSize="lg">
                                Leads
                            </Text>
                            <Button onClick={e => logout()} colorScheme="red" minW="20" leftIcon={<HiLogout />}>
                                Logout
                            </Button>
                        </Flex>
                        <Divider />
                        <Stack spacing="4" py="5" px="8" overflow={'scroll'} maxH={'md'} divider={<StackDivider />}>
                            {
                                usersList.map((user: any, idx: any) => (
                                    <Description
                                        name={user.name}
                                        email={user.email}
                                        phoneNumber={user.phoneNumber}
                                    >
                                        {user.message}
                                    </Description>
                                ))
                            }
                        </Stack>
                    </Box>
                </Box>
            </Box>
            <Box as="section" bg={mode('gray.100', 'gray.800')}>
                <Box maxW={{ base: 'xl', md: '7xl' }} mx="auto" px={{ md: '8' }}>
                    <Box
                        rounded={'lg'}
                        bg={mode('white', 'gray.700')}
                        maxW="7xl"
                        mx="auto"
                        shadow="base"
                        overflow="scroll"
                    >
                        <Flex align="center" justify="space-between" px="6" py="4">
                            <Text as="h3" fontWeight="bold" fontSize="lg">
                                Payment Links
                            </Text>
                            <Button onClick={onOpen} colorScheme="blue" minW="20" leftIcon={<HiPlus />}>
                                Create
                            </Button>
                        </Flex>
                        <Divider />
                        <Stack spacing="4" py="5" px="8" overflow={'scroll'} maxH={'md'} divider={<StackDivider />}>
                            {
                                paymentLinks.map((paymentLink: any, idx: any) => (
                                    <Description
                                        name={paymentLink.customerName}
                                        email={paymentLink.email}
                                        phoneNumber={paymentLink.phoneNumber}
                                        key={idx}
                                    >
                                        <p>Customer Name: {paymentLink.customerName}</p>
                                        <p>Email Address: {paymentLink.email}</p>
                                        <p>Phone Number: {paymentLink.phoneNumber}</p>
                                        <p>Agent Name: {paymentLink.agentName}</p>
                                        <p>Amount: ${paymentLink.amount}</p>
                                    </Description>
                                ))
                            }
                        </Stack>
                    </Box>
                </Box>
            </Box>


            {/* Payment Link Modal */}
            <Modal onClose={onClose} isOpen={isOpen} isCentered>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Create Payment Link</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        <FormControl>
                            <Input placeholder='Agent Name' value={agentName} onChange={e => setAgentName(e.target.value)} />
                        </FormControl>
                        <Stack direction={'row'} mt={4}>
                            <FormControl>
                                <Input placeholder='Customer Name' value={customerName} onChange={e => setCustomerName(e.target.value)} />
                            </FormControl>
                            <FormControl>
                                <Input placeholder='Email Address' value={email} onChange={e => setEmail(e.target.value)} />
                            </FormControl>
                        </Stack>
                        <Stack direction={'row'} mt={4}>
                            <FormControl>
                                <Input placeholder='Phone Number' value={phoneNumber} onChange={e => setPhoneNumber(e.target.value)} />
                            </FormControl>
                            <FormControl>
                                <Input placeholder='Amount' type={'number'} value={amount} onChange={e => setAmount(parseFloat(e.target.value))} />
                            </FormControl>
                        </Stack>
                    </ModalBody>
                    <ModalFooter>
                        <Button isFullWidth onClick={handleCreatePaymentLinkClick} colorScheme={'green'}>Create</Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </Stack>
    )
}